import * as requestFromServer from "./publishersCrud";
import { publishersSlice, callTypes } from "./publishersSlice";
import MockUtils from "../../__mocks__/mock.utils";
import swal from "sweetalert";
const { actions } = publishersSlice;

export const fetchPublishers = (queryParams, initial) => (dispatch) => {
  dispatch(actions.startCall({ callType: callTypes.list }));

  if (queryParams.filter != "") {
    const mockUtils = new MockUtils();

    if (
      queryParams.filter.lastname == "" &&
      queryParams.filter.is_active == ""
    ) {
      const totalCount = initial.length;
      const entities = initial;

      dispatch(actions.publishersFetched({ totalCount, entities }));
    } else {
      const filteredPublishers = mockUtils.baseFilter(initial, queryParams);

      const totalCount = filteredPublishers.totalCount;
      const entities = filteredPublishers.entities;

      dispatch(actions.publishersFetched({ totalCount, entities }));
    }
  } else {
    return requestFromServer
      .getAllPublishers()
      .then((response) => {
        const entities = response.data.data;
        const totalCount = entities.length;
        dispatch(actions.fetchPublishers({ totalCount, entities }));
      })
      .catch((error) => {
        error.clientMessage = "Can't find Publishers";
        dispatch(actions.catchError({ error, callType: callTypes.list }));
      });
  }
};

export const fetchPublisher = (id) => (dispatch) => {
  if (id) {
    return dispatch(actions.publisherFetched({ publisherForEdit: undefined }));
  }

  dispatch(actions.startCall({ callType: callTypes.action }));
  return requestFromServer
    .getPublisherById(id)
    .then((response) => {
      const publisher = response.data;
      dispatch(actions.publisherFetched({ publisherForEdit: publisher }));
    })
    .catch((error) => {
      error.clientMessage = "Can't find publisher";
      dispatch(actions.catchError({ error, callType: callTypes.action }));
    });
};

export const deletePublisher = (id) => (dispatch) => {
  dispatch(actions.startCall({ callType: callTypes.action }));
  return requestFromServer
    .deletePublisher(id)
    .then((response) => {
      dispatch(actions.publisherDeleted({ id }));
      let error_msg = response.data.message;
      alert(error_msg);
    })
    .catch((error) => {
      error.clientMessage = "Can't delete publisher";
      dispatch(actions.catchError({ error, callType: callTypes.action }));
    });
};

export const createPublisher = (publisherForCreation) => (dispatch) => {
  dispatch(actions.startCall({ callType: callTypes.action }));
  return requestFromServer
    .createPublisher(publisherForCreation)
    .then((response) => {
      if (response.data.status === false) {
        let error_msg = response.data.message[0];
        alert(error_msg);
      } else {
        let new_publisher = response.data.data;
        console.log(new_publisher);
        dispatch(actions.publisherCreated({ new_publisher }));
      }
    })
    .catch((error) => {
      error.clientMessage = "Can't create publisher";
      dispatch(actions.catchError({ error, callType: callTypes.action }));
    });
};

export const updatePublisher = (publisher) => (dispatch) => {
  dispatch(actions.startCall({ callType: callTypes.action }));
  return requestFromServer
    .updatePublisher(publisher)
    .then(() => {
      dispatch(actions.publisherUpdated({ publisher }));
    })
    .catch((error) => {
      error.clientMessage = "Can't update publisher";
      dispatch(actions.catchError({ error, callType: callTypes.action }));
    });
};

export const updatePublishersStatus = (ids) => (dispatch) => {
  dispatch(actions.startCall({ callType: callTypes.action }));
  return requestFromServer
    .updateStatusForPublishers(ids)
    .then((response) => {
      swal(response.data.message).then(() => {
        window.location.reload(false);
        // dispatch(actions.publishersStatusUpdated({ ids }));
      });
    })

    .catch((error) => {
      error.clientMessage = "Can't update publishers status";
      dispatch(actions.catchError({ error, callType: callTypes.action }));
    });
};

export const deletePublishers = (ids) => (dispatch) => {
  dispatch(actions.startCall({ callType: callTypes.action }));
  return requestFromServer
    .deletePublishers(ids)
    .then(() => {
      dispatch(actions.publishersDeleted({ ids }));
    })
    .catch((error) => {
      error.clientMessage = "Can't delete publishers";
      dispatch(actions.catchError({ error, callType: callTypes.action }));
    });
};
