import React from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import axios from "axios";
import Action from "../../../../helpers/View";
import { nullChecker } from "../../../../helpers/NullChecker";

export default class RecentWidget extends React.Component {
  state = {
    transactions: [],
  };

  componentDidMount() {
    axios
      .get(`${process.env.REACT_APP_BASE_URL}/all_transactions?count=3`)
      .then((response) => {
        this.setState({ transactions: response.data.data });
      });
  }
  render() {
    const transactions = this.state.transactions;
    const columns = [
      {
        dataField: "transaction_ref",
        text: "TXN",
        style: {
          minWidth: "150px",
        },
      },
      {
        dataField: "payment_currency",
        text: "currency",
        formatter: nullChecker,
      },
      { dataField: "amount_paid", text: "Amount", formatter: nullChecker },
      { dataField: "created_at", text: "Date Initiated" },
      {
        dataField: "updated_at",
        text: "Date Completed",
        formatter: nullChecker,
      },
      { dataField: "status", text: "Status" },
      { dataField: "id", text: "Actions", formatter: Action },
    ];
    return (
      <div className="mt-5 mb-5">
        <div className="card card-custom">
          <div className="card-header flex-wrap border-0 pt-6 pb-0">
            <div className="card-title">
              <h3 className="card-label">Recent Transactions</h3>
            </div>
          </div>
          <div className="card-body">
            {/*begin: Datatable*/}
            <BootstrapTable
              wrapperClasses="table-responsive"
              bordered={false}
              classes="table table-head-custom table-vertical-center mb-5"
              id="kt_datatable_2"
              keyField="transaction_ref"
              bootstrap4
              remote
              data={transactions}
              columns={columns}
              // pagination={paginationFactory()}
            />

            {/*end: Datatable*/}
          </div>
        </div>
        {/*end::Card*/}
      </div>
    );
  }
}
