import React from "react";
import Axios from "axios";
import { Link } from "react-router-dom";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";

class Best_selling_books extends React.Component {
  state = {
    books: [],
  };
  componentDidMount() {
    Axios.get(
      `${process.env.REACT_APP_BASE_URL_USER}/explore/highest_sold?count=5`
    ).then((resp) => {
      this.setState({
        books: resp.data.data,
      });
    });
  }
  render() {
    const books = this.state.books;

    const columns = [
      { dataField: "id", text: "S/N" },
      { dataField: "title", text: "Title" },
      { dataField: "firstname", text: "Firstname" },
      { dataField: "lastname", text: "Lastname" },
      { dataField: "isbn", text: "ISBN" },
      { dataField: "total_purchase", text: "Total Purchase" },
      // { dataField: "id", text: "Actions", formatter: Action },
    ];

    return (
      <div className="">
        {/* begin::Card */}
        <div className="card card-custom">
          <div className="card-header flex-wrap border-0 pt-6 pb-0">
            <div className="card-title">
              <h3 className="card-label">Best Selling Books</h3>
            </div>
          </div>
          <div className="card-body">
            {/*begin: Datatable*/}

            <BootstrapTable
              wrapperClasses="table-responsive"
              bordered={false}
              classes="table table-head-custom table-head-bg table-borderless table-vertical-center mt-5 mb-5"
              id="kt_datatable_2"
              keyField="id"
              bootstrap4
              data={books}
              columns={columns}
              pagination={paginationFactory()}
            />

            {/*end: Datatable*/}
          </div>
        </div>
        {/*end::Card*/}
      </div>
    );
  }
}

export default Best_selling_books;
