import axios from "axios";

export const BUSINESS_URL = process.env.REACT_APP_BASE_URL_BUSINESS;
export const ALL_BUSINESS = "/all_publishers";
export const CREATE_BUSINESS = "/create";
export const UPDATE_BUSINESS = "/edit_publisher/";
export const DELETE_BUSINESS = "/delete_publisher/";

// CREATE =>  POST: add a new publisher to the server
export function createPublisher(publisher) {
  return axios.post(BUSINESS_URL + CREATE_BUSINESS, publisher);
}

// READ
export function getAllPublishers() {
  return axios.get(BUSINESS_URL + ALL_BUSINESS);
}

export function getPublisherById(publisherId) {
  return axios.get(`${BUSINESS_URL}/get_publisher/${publisherId}`);
}

// Method from server should return QueryResultsModel(items: any[], totalsCount: number)
// items => filtered/sorted result
// export function findAdmins(queryParams) {
//   return axios.post(`${BUSINESS_URL}find`, { queryParams });
// }

// UPDATE => PUT: update the publisher on the server
export function updatePublisher(publisher) {
  return axios.post(`${BUSINESS_URL + UPDATE_BUSINESS}${publisher.id}`, {
    publisher,
  });
}

// UPDATE Status
export function updateStatusForPublishers(ids) {
  return axios.put(`${BUSINESS_URL}/toggle_publisher/${ids}`);
}

// DELETE => delete the publisher from the server
export function deletePublisher(publisherId) {
  return axios.get(`${BUSINESS_URL + DELETE_BUSINESS}${publisherId}`);
}

// DELETE Publishers by ids
export function deletePublishers(ids) {
  return axios.post(`${BUSINESS_URL}/deletePublishers`, { ids });
}
