// React bootstrap table next =>
// DOCS: https://react-bootstrap-table.github.io/react-bootstrap-table2/docs/
// STORYBOOK: https://react-bootstrap-table.github.io/react-bootstrap-table2/storybook/index.html
import React, { useEffect, useMemo } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory, {
  PaginationProvider,
} from "react-bootstrap-table2-paginator";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import * as actions from "../../../_redux/publishers/publishersActions";
import {
  getSelectRow,
  getHandlerTableChange,
  NoRecordsFoundMessage,
  PleaseWaitMessage,
  sortCaret,
  headerSortingClasses,
} from "../../../../../../_metronic/_helpers";
import * as uiHelpers from "../PublisherUIHelpers";
import * as columnFormatters from "./column-formatters";
import { Pagination } from "../../../../../../_metronic/_partials/controls";
import { usePublishersUIContext } from "../PublisherUIContext";

export function PublishersTable() {
  // Publishers UI Context
  const publishersUIContext = usePublishersUIContext();
  const publisherUIProps = useMemo(() => {
    return {
      ids: publishersUIContext.ids,
      setIds: publishersUIContext.setIds,
      queryParams: publishersUIContext.queryParams,
      setQueryParams: publishersUIContext.setQueryParams,
      openEditPublisherDialog: publishersUIContext.openEditPublisherDialog,
      openDeletePublisherDialog: publishersUIContext.openDeletePublisherDialog,
    };
  }, [publishersUIContext]);

  // Getting curret state of publishers list from store (Redux)
  const { currentState } = useSelector(
    (state) => ({ currentState: state.publishers }),
    shallowEqual
  );
  const { totalCount, entities, listLoading, initial_entity } = currentState;

  //nullchecker for company name
  const nullChecker = (cell) => (!cell ? "N/A" : cell);

  // Publishers Redux state
  const dispatch = useDispatch();
  useEffect(() => {
    // clear selections list
    publisherUIProps.setIds([]);
    // server call by queryParams
    dispatch(
      actions.fetchPublishers(publisherUIProps.queryParams, initial_entity)
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [publisherUIProps.queryParams, dispatch]);
  // Table columns
  const columns = [
    // {
    //   dataField: "id",
    //   text: "ID",
    //   sort: true,
    //   sortCaret: sortCaret,
    //   headerSortingClasses,
    // },
    {
      dataField: "firstname",
      text: "Firstname",
      sort: true,
      sortCaret: sortCaret,
      headerSortingClasses,
    },
    {
      dataField: "lastname",
      text: "Lastname",
      sort: true,
      sortCaret: sortCaret,
      headerSortingClasses,
    },
    {
      dataField: "email",
      text: "Email",
      sort: true,
      sortCaret: sortCaret,
      headerSortingClasses,
    },
    {
      dataField: "phone",
      text: "Phone",
      sort: true,
      sortCaret: sortCaret,
      headerSortingClasses,
    },
    {
      dataField: "company_name",
      text: "Company Name",
      sort: true,
      sortCaret: sortCaret,
      formatter: nullChecker,
    },
    {
      dataField: "is_active",
      text: "Status",
      sort: true,
      sortCaret: sortCaret,
      formatter: columnFormatters.StatusColumnFormatter,
      headerSortingClasses,
    },

    // create an action for toggle publisher status

    //   {
    //     dataField: "action",
    //     text: "Actions",
    //     formatter: columnFormatters.ActionsColumnFormatter,
    //     formatExtraData: {
    //       openEditPublisherDialog: publisherUIProps.openEditPublisherDialog,
    //       openDeletePublisherDialog: publisherUIProps.openDeletePublisherDialog,
    //     },
    //     classes: "text-center pr-0",
    //     headerClasses: "text-right pr-3",
    //     style: {
    //       minWidth: "100px",
    //     },
    //   },
  ];
  // Table pagination properties
  const paginationOptions = {
    custom: true,
    totalSize: totalCount,
    sizePerPageList: uiHelpers.sizePerPageList,
    sizePerPage: publisherUIProps.queryParams.pageSize,
    page: publisherUIProps.queryParams.pageNumber,
  };
  return (
    <>
      <PaginationProvider pagination={paginationFactory(paginationOptions)}>
        {({ paginationProps, paginationTableProps }) => {
          return (
            <Pagination
              isLoading={listLoading}
              paginationProps={paginationProps}
            >
              <BootstrapTable
                wrapperClasses="table-responsive"
                bordered={false}
                classes="table table-head-custom table-vertical-center"
                bootstrap4
                keyField="id"
                data={entities === null ? [] : entities}
                columns={columns}
                // defaultSorted={uiHelpers.defaultSorted}
                onTableChange={getHandlerTableChange(
                  publisherUIProps.setQueryParams
                )}
                selectRow={getSelectRow({
                  entities,
                  ids: publisherUIProps.ids,
                  setIds: publisherUIProps.setIds,
                })}
                {...paginationTableProps}
              >
                <PleaseWaitMessage entities={entities} />
                <NoRecordsFoundMessage entities={entities} />
              </BootstrapTable>
            </Pagination>
          );
        }}
      </PaginationProvider>
    </>
  );
}
