import React from "react";
import { Route } from "react-router-dom";
import { PublishersLoadingDialog } from "./publisher-loading-dialog/PublisherLoadingDialog";
import { PublisherEditDialog } from "./publisher-edit-dialog/PublisherEditDialog";
import { PublisherDeleteDialog } from "./publisher-delete-dialog/PublisherDeleteDialog";
import { PublishersDeleteDialog } from "./publishers-delete-dialog/PublishersDeleteDialog";
import { PublishersFetchDialog } from "./publisher-fetch-dialog/PublisherFetchDialog";
import { PublishersUpdateStateDialog } from "./publisher-update-status-dialog/PublisherUpdateStatusDialog";
import { PublishersUIProvider } from "./PublisherUIContext";
import { PublishersCard } from "./PublisherCard";

export function PublisherPage({ history }) {
  const publishersUIEvents = {
    newPublisherButtonClick: () => {
      history.push("/management/publishers/new");
    },
    openEditPublisherDialog: (id) => {
      history.push(`/management/publishers/${id}/edit`);
    },
    openDeletePublisherDialog: (id) => {
      history.push(`/management/publishers/${id}/delete`);
    },
    openDeletePublishersDialog: () => {
      history.push(`/management/publishers/deletePublishers`);
    },
    openFetchPublishersDialog: () => {
      history.push(`/management/publishers/fetch`);
    },
    openUpdatePublishersStatusDialog: () => {
      history.push("/management/publishers/updateStatus");
    },
  };

  return (
    <PublishersUIProvider publishersUIEvents={publishersUIEvents}>
      <PublishersLoadingDialog />
      <Route path="/management/publishers/new">
        {({ history, match }) => (
          <PublisherEditDialog
            show={match != null}
            onHide={() => {
              history.push("/management/publishers");
            }}
          />
        )}
      </Route>
      <Route path="/management/publishers/:id/edit">
        {({ history, match }) => (
          <PublisherEditDialog
            show={match != null}
            id={match && match.params.id}
            onHide={() => {
              history.push("/management/publishers");
            }}
          />
        )}
      </Route>
      <Route path="/management/publishers/deletePublishers">
        {({ history, match }) => (
          <PublishersDeleteDialog
            show={match != null}
            onHide={() => {
              history.push("/management/publishers");
            }}
          />
        )}
      </Route>
      <Route path="/management/publishers/:id/delete">
        {({ history, match }) => (
          <PublisherDeleteDialog
            show={match != null}
            id={match && match.params.id}
            onHide={() => {
              history.push("/management/publishers");
            }}
          />
        )}
      </Route>
      <Route path="/management/publishers/fetch">
        {({ history, match }) => (
          <PublishersFetchDialog
            show={match != null}
            onHide={() => {
              history.push("/management/publishers");
            }}
          />
        )}
      </Route>
      <Route path="/management/publishers/updateStatus">
        {({ history, match }) => (
          <PublishersUpdateStateDialog
            show={match != null}
            onHide={() => {
              history.push("/management/publishers");
            }}
          />
        )}
      </Route>
      <PublishersCard />
    </PublishersUIProvider>
  );
}
