import React from "react";
import Axios from "axios";
import { Link } from "react-router-dom";

class Latest_books extends React.Component {
  state = {
    books: [],
  };
  componentDidMount() {
    Axios.get(`${process.env.REACT_APP_BASE_URL_USER}/get_books?count=3`).then(
      (resp) => {
        this.setState({
          books: resp.data.data,
        });
      }
    );
  }
  render() {
    const books = this.state.books;

    return (
      <div>
        <div className="mt-5 mb-5">
          <div className={`card card-custom `}>
            {/* Head */}
            <div className="card-header border-0 pt-5">
              <h3 className="card-title align-items-start flex-column">
                <span className="card-label font-weight-bolder text-dark">
                  Latest Catalogues Uploaded
                </span>
              </h3>
            </div>
            {/* Body */}
            <div className="card-body pt-3 pb-0">
              <div className="table-responsive">
                <table className="table table-borderless table-vertical-center">
                  <thead>
                    <tr>
                      <th className="p-0" style={{ width: "50px" }} />
                      <th className="p-0" style={{ minWidth: "125px" }} />
                      <th className="p-0" style={{ minWidth: "100px" }} />
                      <th className="p-0" style={{ minWidth: "125px" }} />
                      <th className="p-0" style={{ minWidth: "110px" }} />
                    </tr>
                  </thead>
                  <tbody>
                    {books &&
                      books.map((book) => {
                        return (
                          <tr>
                            <td className="pl-0 py-4">
                              <div className="symbol symbol-50">
                                <span className="symbol-label">
                                  <img
                                    src={`${process.env.REACT_APP_IMAGE_URL}/${book.front_cover}`}
                                    className="img-fluid symbol"
                                    style={{ height: "70px" }}
                                    alt=""
                                  />
                                </span>
                              </div>
                            </td>
                            <td className="pl-0">
                              <span className="text-dark-75 font-weight-bolder text-hover-primary mb-1 font-size-lg">
                                {book.title ? book.title : "Untitled"}
                              </span>
                            </td>
                            <td className="text-right">
                              <span className="text-muted font-weight-500">
                                {book.firstname}
                              </span>
                            </td>
                            <td className="text-right">
                              <span className="text-muted font-weight-500">
                                {book.lastname}
                              </span>
                            </td>

                            <td className="text-right">
                              <span className="text-dark-75 font-weight-bolder d-block font-size-lg">
                                {/* {book.artwork_currency === "naira"
                                  ? ""
                                  : book.artwork_currency === "pounds"
                                  ? ""
                                  : book.artwork_currency === "euro"
                                  ? ""
                                  : "$"} */}
                                {book.price}
                              </span>
                            </td>
                            <td className="text-right">
                              <span className="text-muted font-weight-500">
                                {book.isbn}
                              </span>
                            </td>
                            {/* <td className="text-right">
                              <Link
                                to={"/management/catalogue/" + book.id}
                                className="text-primary font-weight-bolder"
                              >
                                <i className="fa fa-eye"></i> View Artwork
                              </Link>
                            </td> */}
                          </tr>
                        );
                      })}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Latest_books;
