import React, { useEffect, useState, useMemo } from "react";
import { Modal } from "react-bootstrap";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { PublisherStatusCssClasses } from "../PublisherUIHelpers";
import * as actions from "../../../_redux/publishers/publishersActions";
import { usePublishersUIContext } from "../PublisherUIContext";

const selectedPublishers = (entities, ids) => {
  const _publishers = [];
  ids.forEach((id) => {
    const publisher = entities.find((el) => el.id === id);
    if (publisher) {
      _publishers.push(publisher);
    }
  });
  return _publishers;
};

export function PublishersUpdateStateDialog({ show, onHide }) {
  // Publishers UI Context
  const publishersUIContext = usePublishersUIContext();
  const publishersUIProps = useMemo(() => {
    return {
      ids: publishersUIContext.ids,
      setIds: publishersUIContext.setIds,
      queryParams: publishersUIContext.queryParams,
    };
  }, [publishersUIContext]);

  // publishers Redux state
  const { publishers, isLoading } = useSelector(
    (state) => ({
      publishers: selectedPublishers(
        state.publishers.entities,
        publishersUIProps.ids
      ),
      isLoading: state.publishers.actionsLoading,
    }),
    shallowEqual
  );

  // if !id we should close modal
  useEffect(() => {
    if (!publishersUIProps.ids || publishersUIProps.ids.length === 0) {
      onHide();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [publishersUIProps.ids]);

  const [status, setStatus] = useState(0);

  const dispatch = useDispatch();
  const updateStatus = () => {
    // server request for update Publishers status by selected ids
    dispatch(actions.updatePublishersStatus(publishersUIProps.ids)).then(() => {
      // refresh list after deletion
      // dispatch(actions.fetchPublishers(publishersUIProps.queryParams)).then(() => {
      // clear selections list
      // publishersUIProps.setIds();
      // closing delete modal
      onHide();
      // });
    });
  };

  // console.log(publishers);

  return (
    <Modal
      show={show}
      onHide={onHide}
      aria-labelledby="example-modal-sizes-title-lg"
    >
      <Modal.Header closeButton>
        <Modal.Title id="example-modal-sizes-title-lg">
          Status would been updated for selected publisher
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="overlay overlay-block">
        {/*begin::Loading*/}
        {isLoading && (
          <div className="overlay-layer">
            <div className="spinner spinner-lg spinner-primary" />
          </div>
        )}
        {/*end::Loading*/}

        <div className="timeline timeline-5 mt-3">
          {publishers.map((publisher) => (
            <div
              className="timeline-item align-items-start"
              key={`publishersUpdate${publisher.id}`}
            >
              <div className="timeline-label font-weight-bolder text-dark-75 font-size-lg text-right pr-3" />
              <div className="timeline-badge">
                <i
                  className={`fa fa-genderless text-${
                    PublisherStatusCssClasses[publisher.status]
                  } icon-xxl`}
                />
              </div>
              <div className="timeline-content text-dark-50 mr-5">
                <span
                  className={`label label-lg label-light-${
                    PublisherStatusCssClasses[publisher.status]
                  } label-inline`}
                >
                  ID: {publisher.id}
                </span>
                <span className="ml-3">
                  {publisher.firstname} {publisher.lastname}
                </span>
              </div>
            </div>
          ))}
        </div>
      </Modal.Body>
      <Modal.Footer className="form">
        {/* <div className="form-group">
          <select
            className="form-control"
            value={status}
            onChange={(e) => setStatus(+e.target.value)}
          >
            <option value="0">Inactive</option>
            <option value="1">Active</option>
          </select>
        </div> */}
        <div className="form-group">
          <button
            type="button"
            onClick={onHide}
            className="btn btn-light btn-elevate mr-3"
          >
            Cancel
          </button>
          <button
            type="button"
            onClick={updateStatus}
            className="btn btn-primary btn-elevate"
          >
            Update Status
          </button>
        </div>
      </Modal.Footer>
    </Modal>
  );
}
