import React from "react";
import Highcharts from "highcharts/highstock";
import HighchartsReact from "highcharts-react-official";
import Axios from "axios";

class CustomerWidget extends React.Component {
  state = {
    customers: "",
    days: "",
  };

  // componentDidMount() {
  //   this.get_data();
  // }
  // get_data = async () => {
  //   const resp = await Axios.get(
  //     `${process.env.REACT_APP_BASE_URL_USER}/get_transactions_by_period`
  //   );
  //   this.get_series(resp.data.data);
  // };

  get_series(result) {
    let initial = [];
    let days = [];
    result.filter((role) => {
      let customers = role.user_signups;
      let day = role.curr_day;
      initial.push(customers);
      days.push(day);
    });
    this.setState({
      customers: initial,
      days: days,
    });
  }

  render() {
    const options = {
      chart: {
        zoomType: "x",
      },
      title: {
        text: "Customers",
      },
      xAxis: {
        categories: this.state.days,
      },
      yAxis: {
        title: {
          text: "Customers",
        },
      },
      rangeSelector: {
        enabled: false,
      },
      series: [
        {
          type: "line",
          data: this.state.customers,
          allowPointSelect: true,
        },
      ],
      credits: {
        enabled: false,
      },
    };

    return (
      <div className="mt-5 mb-5">
        <HighchartsReact
          highcharts={Highcharts}
          constructorType={"stockChart"}
          options={options}
        />
      </div>
    );
  }
}
export default CustomerWidget;
