import React, { useMemo } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  CardHeaderToolbar,
} from "../../../../../_metronic/_partials/controls";
import { PublishersFilter } from "./publisher-filter/PublisherFilter";
import { PublishersTable } from "./publisher-table/PublisherTable";
import { PublishersGrouping } from "./publisher-grouping/PublisherGrouping";
import { usePublishersUIContext } from "./PublisherUIContext";

export function PublishersCard() {
  const publishersUIContext = usePublishersUIContext();
  const publishersUIProps = useMemo(() => {
    return {
      ids: publishersUIContext.ids,
      newpublisherButtonClick: publishersUIContext.newPublisherButtonClick,
    };
  }, [publishersUIContext]);

  return (
    <Card>
      <CardHeader title="Publishers list">
        <CardHeaderToolbar>
          <button
            type="button"
            className="btn btn-primary"
            onClick={publishersUIContext.newPublisherButtonClick}
          >
            New Publisher
          </button>
        </CardHeaderToolbar>
      </CardHeader>
      <CardBody>
        <PublishersFilter />
        {publishersUIProps.ids.length > 0 && <PublishersGrouping />}
        <PublishersTable />
      </CardBody>
    </Card>
  );
}
