import React from "react";
import Highcharts from "highcharts/highstock";
import HighchartsReact from "highcharts-react-official";
import Axios from "axios";
import moment from "moment";

class TransactionWidget extends React.Component {
  state = {
    transactions: "",
    days: "",
  };

  componentDidMount() {
    this.get_data();
  }
  get_data = async () => {
    const resp = await Axios.get(
      `${process.env.REACT_APP_BASE_URL_USER}/get_transactions_by_period`
    );
    this.get_series(resp.data.data);
  };

  get_series(result) {
    let initial = [];
    let days = [];
    result.filter((role) => {
      let each_amount = Number(role.amount_paid);
      let day = moment(role.updated_at).format("Do-MM-YYYY");
      initial.push(each_amount);
      days.push(day);
    });
    this.setState({
      transactions: initial,
      days: days,
    });
  }

  render() {
    // console.log(this.state.transactions);
    const options = {
      chart: {
        zoomType: "x",
      },
      title: {
        text: "Transactions",
      },
      xAxis: {
        categories: this.state.days,
      },
      yAxis: {
        title: {
          text: "Amount",
        },
      },
      rangeSelector: {
        enabled: false,
      },

      series: [
        {
          type: "line",
          data: this.state.transactions,
          allowPointSelect: true,
        },
      ],
      credits: {
        enabled: false,
      },
    };
    return (
      <div className="mt-5 mb-5">
        <HighchartsReact
          highcharts={Highcharts}
          constructorType={"stockChart"}
          options={options}
        />
      </div>
    );
  }
}

export default TransactionWidget;
