import React, { useState, useEffect } from "react";
import { shallowEqual, useSelector } from "react-redux";
import { Modal } from "react-bootstrap";
import { ModalProgressBar } from "../../../../../../_metronic/_partials/controls";

export function PublisherEditDialogHeader({ id }) {
  // Publishers Redux state
  const { publisherForEdit, actionsLoading } = useSelector(
    (state) => ({
      publisherForEdit: state.publishers.publisherForEdit,
      actionsLoading: state.publishers.actionsLoading,
    }),
    shallowEqual
  );

  const [title, setTitle] = useState("");
  // Title couting
  useEffect(() => {
    let _title = id ? "" : "New Publisher";
    if (publisherForEdit && id) {
      _title = `Edit publisher '${publisherForEdit.firstName} ${publisherForEdit.lastName}'`;
    }

    setTitle(_title);
    // eslint-disable-next-line
  }, [publisherForEdit, actionsLoading]);

  return (
    <>
      {actionsLoading && <ModalProgressBar />}
      <Modal.Header closeButton>
        <Modal.Title id="example-modal-sizes-title-lg">{title}</Modal.Title>
      </Modal.Header>
    </>
  );
}
