import React, { useEffect, useMemo } from "react";
import { Modal } from "react-bootstrap";
import { shallowEqual, useSelector } from "react-redux";
import { PublisherStatusCssClasses } from "../PublisherUIHelpers";
import { usePublishersUIContext } from "../PublisherUIContext";

const selectedPublishers = (entities, ids) => {
  const _publishers = [];
  ids.forEach((id) => {
    const publisher = entities.find((el) => el.id === id);
    if (publisher) {
      _publishers.push(publisher);
    }
  });
  return _publishers;
};

export function PublishersFetchDialog({ show, onHide }) {
  // Publishers UI Context
  const publishersUIContext = usePublishersUIContext();
  const publishersUIProps = useMemo(() => {
    return {
      ids: publishersUIContext.ids,
    };
  }, [publishersUIContext]);

  // publishers Redux state
  const { publishers } = useSelector(
    (state) => ({
      publishers: selectedPublishers(
        state.publishers.entities,
        publishersUIProps.ids
      ),
    }),
    shallowEqual
  );

  // if publishers weren't selected we should close modal
  useEffect(() => {
    if (!publishersUIProps.ids || publishersUIProps.ids.length === 0) {
      onHide();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [publishersUIProps.ids]);

  return (
    <Modal
      show={show}
      onHide={onHide}
      aria-labelledby="example-modal-sizes-title-lg"
    >
      <Modal.Header closeButton>
        <Modal.Title id="example-modal-sizes-title-lg">
          Fetch selected elements
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="timeline timeline-5 mt-3">
          {publishers.map((publisher) => (
            <div
              className="timeline-item align-items-start"
              key={`id${publisher.id}`}
            >
              <div className="timeline-label font-weight-bolder text-dark-75 font-size-lg text-right pr-3" />
              <div className="timeline-badge">
                <i
                  className={`fa fa-genderless text-${
                    PublisherStatusCssClasses[publisher.status]
                  } icon-xxl`}
                />
              </div>
              <div className="timeline-content text-dark-50 mr-5">
                <span
                  className={`label label-lg label-light-${
                    PublisherStatusCssClasses[publisher.status]
                  } label-inline`}
                >
                  ID: {publisher.id}
                </span>
                <span className="ml-3">
                  {publisher.lastname}, {publisher.firstname}
                </span>
              </div>
            </div>
          ))}
        </div>
      </Modal.Body>
      <Modal.Footer>
        <div>
          <button
            type="button"
            onClick={onHide}
            className="btn btn-light btn-elevate"
          >
            Cancel
          </button>
          <> </>
          <button
            type="button"
            onClick={onHide}
            className="btn btn-primary btn-elevate"
          >
            Ok
          </button>
        </div>
      </Modal.Footer>
    </Modal>
  );
}
