import React, { useEffect, useMemo } from "react";
import { Modal } from "react-bootstrap";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import * as actions from "../../../_redux/publishers/publishersActions";
import { PublisherEditDialogHeader } from "./PublisherEditDialogHeader";
import { PublisherEditForm } from "./PublisherEditForm";
import { usePublishersUIContext } from "../PublisherUIContext";

export function PublisherEditDialog({ id, show, onHide }) {
  // Publishers UI Context

  const publishersUIContext = usePublishersUIContext();
  const publishersUIProps = useMemo(() => {
    return {
      initPublisher: publishersUIContext.initPublisher,
    };
  }, [publishersUIContext]);

  // Publishers Redux state
  const dispatch = useDispatch();
  const { actionsLoading, publisherForEdit } = useSelector(
    (state) => ({
      actionsLoading: state.publishers.actionsLoading,
      publisherForEdit: state.publishers.publisherForEdit,
    }),
    shallowEqual
  );

  useEffect(() => {
    // server call for getting publisher by id
    dispatch(actions.fetchPublisher(id));
  }, [id, dispatch]);

  // server request for saving Publisher
  const savePublisher = (publisher) => {
    if (!id) {
      // server request for creating publisher
      dispatch(actions.createPublisher(publisher)).then(() => onHide());
    } else {
      // server request for updating publisher
      dispatch(actions.updatePublisher(publisher)).then(() => onHide());
    }
  };

  return (
    <Modal
      size="lg"
      show={show}
      onHide={onHide}
      aria-labelledby="example-modal-sizes-title-lg"
    >
      <PublisherEditDialogHeader id={id} />
      <PublisherEditForm
        savePublisher={savePublisher}
        actionsLoading={actionsLoading}
        publisher={publisherForEdit || publishersUIProps.initPublisher}
        onHide={onHide}
      />
    </Modal>
  );
}
